import React, { useState, useEffect } from 'react';

const TextFileDisplay = ({ pathname, filename }) => {
  const [fileContent, setFileContent] = useState('');

  useEffect(() => {
    const fetchFile = async () => {
      try {
        console.log(`${pathname}/data/${filename}`)
        const response = await fetch(`${pathname}/data/${filename}`);
        const text = await response.text();
        setFileContent(text);
      } catch (error) {
        console.error('Error fetching file:', error);
      }
    };
    fetchFile();
  }, [filename]);

  return (
    
    <div className="half-width">
      <pre className="half-width left-aligned">{fileContent}</pre>
    </div>
  );
};

export default TextFileDisplay;

